<template>
    <div id="infocenter">
        <!--  -->
        <HeadTop title="">
            <span slot="left">
                <div @click="routerback">
                    <Icon type="md-arrow-round-back" size="20" /><span></span>
                </div>
            </span>
            <span slot="right"></span>
        </HeadTop>

        <!--  -->
        <div>
          <div class="upload">
            <!--  start -->
            <div style="width:280px;margin:0 auto;text-align:center;">
              <!--  start -->
              <div v-if="showInfo">
                <Form :model="info" label-position="right" :label-width="100">
                    <img :src="this.info.avatarurl" width="60" height="60"/>
                    <FormItem label=":"  style="text-align:left;">
                        {{this.info.username}}
                    </FormItem>
                    <FormItem label=":"  style="text-align:left;">
                        {{this.info.nickname}}
                    </FormItem>
                    <FormItem label=":"  style="text-align:left;">
                        {{this.info.sex}}
                    </FormItem>
                    <FormItem label=":"  style="text-align:left;">
                        {{this.info.age}}
                    </FormItem>
                    <FormItem label=":"  style="text-align:left;">
                        {{this.info.disc}}
                    </FormItem>
                    <FormItem label=":"  style="text-align:left;">
                        {{this.info.address}}
                    </FormItem>
                </Form>
                <Button type="warning" @click="changeInfo" ghost></Button>
              </div>
              <!--  end -->
              
              <!--  start -->
              <div v-if="showForm">
                <Form :model="info" label-position="left" :label-width="100">
                    <div>
                      <img @click.stop="uploadHeadImg" :src="this.info.avatarurl" width="60" height="60"/>
                      <input type="file" accept="image/*" @change="handleFile" id="hiddenInput" />
                    </div>
                    <FormItem label="">
                        {{this.info.username}}
                    </FormItem>
                    <FormItem label="">
                        <Input v-model="info.nickname" />
                    </FormItem>
                    <FormItem label=":"  style="text-align:left;">
                        <select  v-model="info.sex"  @change="changeProduct($event)" >
                            <option v-for="(sex,index) in sexList" :key="index"  :value="sex.value">{{sex.value}}</option>  
                        </select>  
                    </FormItem>
                    <FormItem label=":"  style="text-align:left;">
                        <Input v-model="info.age" />
                    </FormItem>
                    <FormItem label=":">
                        <Input type="textarea" v-model="info.disc" :autosize="{minRows: 2,maxRows: 5}" />
                    </FormItem>
                    <FormItem label=":"  style="text-align:left;">
                        <Input v-model="info.address" />
                    </FormItem>
                </Form>
                <Button type="success" @click="sureInfo" ghost></Button>
                <Button type="info" @click="cancelInfo" ghost></Button>
              </div>
              <!--  end -->

              
            </div>
            <!--  end -->

          </div>
        </div>

    </div>
</template>

<script>
import store from '../../vuex/store'
import HeadTop from '../components/HeadTop/HeadTop'

export default {
    name:"Infocenter",
    data(){
        return {
            //  
            showInfo:true,
            showForm:false,
            //(query),
            info:{},
            //
            oldinfo:{},
            //
            pic_file:{},
            //:
            sexList:[
              {
                value:''
              },
              {
                value:''
              }
              
            ],
            
            //
            content: '',//
            maxSize: 10240000 / 2,//
            maxCount: 4,//
            filesArr: [],//
            images: []//base64
        }
    },
    //vuex
    store,
    //
    components: {
        HeadTop,
    },
    //
    methods:{  
        //
        changeProduct(event) {
            console.log('')
            console.log(event.target.value)
            // this.ProductActive = event.target.value; 
        },
        routerback(){//
            this.$router.back(-1);
        },
        changeInfo(){// 
          this.showInfo = false;
          this.showForm = true;
        },
        sureInfo(){//
          // 
          this.showInfo = true;
          this.showForm = false;
          //file--,username--,disc--,_id--
          //nickname--,sex--,age--,address--
          var formData=new FormData();// form:formData,req.body
          formData.append('file',this.pic_file);
          formData.append('username', this.info.username);
          formData.append('avatarurl', this.info.avatarurl);
          formData.append('disc', this.info.disc);
          formData.append('nickname', this.info.nickname);
          formData.append('sex', this.info.sex);
          formData.append('age', this.info.age);
          formData.append('address', this.info.address);
          formData.append('_id', this.info._id);
          console.log('formData')
          console.log(formData)
          this.$axios({
            url:'http://193.112.48.157:3333/uploadFile', 
            method:'post',
            data:formData, 
            headers: {
              "Content-Type": "application/x-www-form-urlencoded"
            }
          })
          .then((res) => {//res.data
            this.$Message.info(res.data.msg);
            this.info.avatarurl = res.data.avatarurl;
            // cancelInfo()
            this.oldinfo = Object.assign({}, this.info);            
          })
          .catch(err=>{
              this.$Message.info('');
              //
              this.info = Object.assign({}, this.oldinfo);
              console.log(' ');
              console.log(err);
          })

        },
        cancelInfo(){ // 
          // 
          this.showInfo = true;
          this.showForm = false;
          //
          this.info = Object.assign({}, this.oldinfo);
        },
        //
        // 
        uploadHeadImg: function () {
          this.$el.querySelector('#hiddenInput').click();
        },
        //  ()
        handleFile: function (e) {
          let $target = e.target || e.srcElement;//input
          let file = $target.files[0];// 
          this.pic_file = $target.files[0];//data
          // 
          var reader = new FileReader();//,
          reader.onload = (data) => {//
            let res = data.target || data.srcElement;//
            this.info.avatarurl = res.result;//(),infoavatarurl
          };
          reader.readAsDataURL(file);//--Base64
        },
        


    },//methods-end
    /**
     * 
     */
    created(){
//select
        this.info.sex = this.sexList[0].value;
    },
    //beforeCreate    data(){} 
    beforeCreate(){
        //token
        var localtoken = localStorage.getItem("token");
        var localusername = localStorage.getItem("user_name");
        var localparams = {
            token:localtoken,
            user_name:localusername
        };
          this.$axios({
                url:"http://193.112.48.157:3333/users/islogin",
                method:"get",
                params:localparams 
              })
              .then(res=>{
                  console.log('vue[  ]');
                  if(res.data.status == 200){
                      this.info = res.data.info;
                      //
                      this.oldinfo = Object.assign({}, this.info);
                      console.log(res.data.info);
                  }
              })
              .catch(err=>{
                  console.log(err)
              })
    },//beforreCreate-end

}
</script>

<style  scoped>
#hiddenInput{
  display: none;
}

</style>



