<template>
    <div id="me">
        <!--  -->
        <HeadTop title="">
            <span slot="left">
            </span>
            <span slot="right">
                <Icon type="md-settings" size="20" />
            </span>
        </HeadTop>

        <div class="content">
            <!--  -->
            <div class="info">
                <!--  -->
                <div class="info_content" v-if="!showInfo">
                    <div class="info_img">
                        <Icon type="md-outlet" size="60" style="color:#c5c8ce;"/>
                    </div><br>
                    <div class="info_operation">
                        <router-link :to="{path:'/login',query:{}}"> | </router-link>
                    </div><br>
                    <div class="info_fan">
                        <span>0</span>&nbsp;&nbsp;
                        <span>0</span>
                    </div>
                </div>
                <!--  -->
                <div class="info_content" v-if="showInfo">
                    <div class="info_img">
                        <!--  -->
                        <img :src="this.person.avatarurl" width="60" height="60"/>
                    </div><br>
                    <div class="info_operation">
                        <router-link :to="{path:'/infocenter',query:{person:this.person}}"><span class="info_color">:{{person.user_name}}</span></router-link><br />
                        <router-link :to="{path:'/infocenter',query:{person:this.person}}"><span class="info_color">:{{person.nickname}}</span></router-link>
                    </div>
                    <div class="info_disc">
                        <span class="info_color">{{person.disc}}</span>
                    </div>
                    <div class="info_fan">
                        <span class="info_color">{{person.fan}}</span>&nbsp;&nbsp;
                        <span class="info_color">{{person.cult}}</span>
                    </div>
                </div>
            </div>

            <!--  -->
            <div >
                <Tabs value="name1" class="allopts">
                    <!--  -->
                    <TabPane label="" name="name1" class="opts">
                        <div style="padding: 10px;background: #f8f8f9margin:0 auto;width:100%;">
                            <Card title="" icon="ios-options" :padding="0" shadow style="width: 100%;">
                                <CellGroup>
                                    <Cell >
                                        <div >
                                            <router-link to='/infocenter' style="color:#515A6E;">
                                                <Icon type="md-checkbox" class="iconColor" />&nbsp;&nbsp;
                                                <span></span>
                                            </router-link>
                                        </div>
                                    </Cell>
                                    <Cell  >
                                        <div>
                                            <router-link to='/postings' style="color:#515A6E;">
                                                <Icon type="md-list-box" class="iconColor" />&nbsp;&nbsp;
                                                <span></span>
                                            </router-link>
                                        </div>
                                    </Cell>
                                    <Cell  >
                                        <div @click="getmedal">
                                            <Icon type="logo-twitch" class="iconColor" />&nbsp;&nbsp;
                                            <span></span>
                                        </div>
                                    </Cell>
                                    <Cell  >
                                        <div @click="getmedal">
                                            <Icon type="ios-paper" class="iconColor" />&nbsp;&nbsp;
                                            <span></span>
                                        </div>
                                    </Cell>
                                    <Cell  >
                                        <div @click="getmedal">
                                            <Icon type="md-ionitron" class="iconColor" />&nbsp;&nbsp;
                                            <span></span>
                                        </div>
                                    </Cell>
                                    <Cell class="testsss"  on-click="getmedal">
                                        <div @click="getmedal">
                                            <Icon type="md-ribbon" class="iconColor" />&nbsp;&nbsp;
                                            <span></span>
                                        </div>
                                    </Cell> 
                                    <Cell class="testsss"  on-click="getmedal">
                                        <div @click="logout">
                                            <Icon type="md-log-out" style="color:#ed4014;" />&nbsp;&nbsp;
                                            <span></span>
                                        </div>
                                    </Cell>       
                                </CellGroup>
                            </Card>
                        </div>
                    </TabPane>
                    <!--  -->
                    <TabPane label="" name="name2" class="opts">
                        <div class="says">
                            <Form :model="sayIt" label-position="left" :label-width="100">
                            <FormItem label=":">
                                <Input type="textarea" v-model="sayIt.saycontent" :autosize="{minRows: 2,maxRows: 5}" placeholder=",come on!" />
                            </FormItem>
                        </Form>
                        <div class="picture">
                            <ul>
                                <li ref="files" class="uploader__file" v-for="(image,index) in images" :key="index">
                                    <Icon type="md-close-circle"  @click="deleteimg(index)" size="20" style="color:red;" />
                                    <img :src="image" alt="" width="350" height="350">
                                    <br><br>
                                </li>
                            </ul>
                            <!--  -->
                            <div style="clear:both;"></div>
                            <!-- brmargin5px -->
                            <br/><br/>
                            <input @change="change" value=""  type="file" multiple accept="image/*">
                        </div>
                        <br/>
                        <Button type="success" @click="publish" ghost></Button>
                        </div>
                    </TabPane>
                    <!--  -->
                    <TabPane label="" name="name3" class="opts"></TabPane>
                </Tabs>
            </div>

        </div>

        <TabBar></TabBar>
    </div>
</template>

<script>
import store from '../../vuex/store'
import TabBar from '../components/TabBar/TabBar'
import HeadTop from '../components/HeadTop/HeadTop'

export default {
    name:"Me",
    data(){
        return {
            showInfo:false,
            // 
            person:{
                nickname:'',
                user_name:'',
                fan:0,
                cult:0,
                disc:'',
                avatarurl:'',
            },
            //
            sayIt:{
                saycontent:'',
                avatarurl:[],
            },
            //
            maxSize: 10240000 / 2,//
            maxCount: 9,//
            filesArr: [],//
            images: []//base64
            
        }
    },
    methods:{
        // 
        logout(){
            //div.info
            var localtoken = localStorage.getItem("token");
            var localusername = localStorage.getItem("user_name");
            var localparams = {
                token:localtoken,
                user_name:localusername
            }
            this.$axios({
                  url:"http://193.112.48.157:3333/users/islogin",
                  method:"get",
                  params:localparams 
                })
                .then(res=>{
                    console.log('vue');
                    if(res.data.status == 200){
                        this.showInfo = true;
                        localStorage.setItem('token', null);
                        localStorage.setItem('user_name', null);
                        localStorage.setItem('info', null);
                        this.$Message.info('');
                        this.$router.push('/login');
                    }else{
                        this.$Message.info('')
                        this.$router.push('/login');
                    }
                })
                .catch(err=>{
                    console.log(err)
                })
            
        },
        //
        publish(){
            var formData=new FormData();// form:formData,req.body
            formData.append('content',this.sayIt.saycontent);
            formData.append('username',this.person.user_name);
            //formData
            this.filesArr.forEach((file) => {
                formData.append('pictures', file);
            });
            this.$axios({
                  url:"http://193.112.48.157:3333/users/says",
                  method:"post",
                  data:formData, 
                    headers: {
                    "Content-Type": "application/x-www-form-urlencoded"
                    }
                })
                .then(res=>{
                    console.log('');
                    console.log(res.data);
                    //
                    if(res.data.status == 200){
                        //input
                        this.sayIt = {
                            saycontent:'',
                            avatarurl:[],
                        };
                        //
                        this.images = [];
                        this.filesArr = [];
                        //,
                        this.$Message.info(res.data.msg);
                        this.$router.push('/me');
                    }else{
                      this.$Message.info(res.data.msg);
                      this.$router.push('/me');
                    }
                })
                .catch(err=>{
                    console.log(err)
                })
        },
        // --
        //
        deleteimg(index) {
            this.filesArr.splice(index, 1);
            this.images.splice(index, 1);
        },
        //
        change(e) {
            let files = e.target.files;
            // 
            if (files.length === 0) {
                return;
            }
            if (this.images.length + files.length > this.maxCount) {
                this.$Message.info('' + this.maxCount + '');
                return;
            }
            let reader;
            let file;
            let images = this.images;
            for (let i = 0; i < files.length; i++) {
                file = files[i];
                this.filesArr.push(file);//
                //H5:FileReader
                reader = new FileReader();
                if (file.size > self.maxSize) {
                    this.$Message.info('');
                    continue;
            }
            //onload	
            reader.onload = (e) => {
                let img = new Image();
                img.onload = function () {
                    let canvas = document.createElement('canvas');
                    //getContext() 
                    // "2d" API
                    let ctx = canvas.getContext('2d');
                    let w = img.width;
                    let h = img.height;
                    //  canvas 
                    canvas.width = w;
                    canvas.height = h;
                    ctx.drawImage(img, 0, 0, w, h);
                    //canvas.toDataURL(type, encoderOptions);
                    //image/png01dataURIDOMString
                    //dataURI.toDataURL()<img>onload
                    let base64 = canvas.toDataURL('image/png');
                    images.push(base64);
                };
                //e.target
                img.src = e.target.result;
            };
            //FileReaderreadAsDataURLData URL
            reader.readAsDataURL(file);
            }
        },// --
        

        // 
        getmedal(value){
            console.log(value);
            this.$axios({
                  url:"http://193.112.48.157:3333/users/me/medal",
                  method:"get",
                  params:{"name":""}
                })
                .then(res=>{
                    console.log('me.vue');
                    console.log(res.data);
                    //
                    if(res.data.status == 200){
                        this.$Message.info(res.data.msg);
                    }else{
                      this.$Message.info(res.data.msg);
                      this.$router.push('/login');
                    }
                })
                .catch(err=>{
                    console.log(err)
                })
        }
    },//methods
    //vuex
    store,
    //
    components: {
        HeadTop,
        TabBar,
    },
    //vue
    beforeCreate(){
        //div.info
        var localtoken = localStorage.getItem("token");
        var localusername = localStorage.getItem("user_name");
        var localparams = {
            token:localtoken,
            user_name:localusername
        }
        this.$axios({
                  url:"http://193.112.48.157:3333/users/islogin",
                  method:"get",
                  params:localparams 
                })
                .then(res=>{
                    console.log('vue');
                    if(res.data.status == 200){
                        this.showInfo = true;
                        this.person.user_name = res.data.info.username;
                        this.person.nickname = res.data.info.nickname;
                        this.person.disc = res.data.info.disc;
                        this.person.fan = res.data.info.fan;
                        this.person.cult = res.data.info.cult;
                        this.person.avatarurl = res.data.info.avatarurl;
                        console.log(res.data.info);
                    }
                })
                .catch(err=>{
                    console.log(err)
                })
    },
}
</script>

<style scoped>
.content{
    width:100%;
    margin: 0 auto;
}
/*  */
.info{
    height: 200px;
    width: 100%;
    background-image: url('../../static/images/starssky.jpeg');
}
.info_content{
    width:400px;
    height:200px;
    padding:30px;
    margin: 0 auto;
}
.info_operation a{
    color:white;
}
.info_fan{
    color:white;
}
.info_disc{
    color:white;
}
.info_color{
    color:white;
}
/*  */
.allopts{
    width: 100%;
    margin: 0 auto;
}

/*  */
.iconColor{
    color:#5cadff;
}
/*  */
.says{
    width:80%;
    margin:0 auto;
}
ul,li{
    list-style: none;
    float:left;
}
/*  */
.uploader__file{
    margin:5px;
    width:350px;
    height:350px;
}
</style>



