<template>
    <div id="postings">
        <!--  -->
        <HeadTop title="">
            <span slot="left">
                <div @click="routerback">
                    <Icon type="md-arrow-round-back" size="20" /><span></span>
                </div>
            </span>
            <span slot="right"></span>
        </HeadTop>
        <br/>
        <!--  -->
        <div class="Pcontainer">
            <ul>
                <li v-for="(posting,index) in postings" :key="index" class="feed">
                    <!--  -->
                    <Modal
                        v-model="modal"
                        title=""
                        @on-ok="ok(posting._id)"
                        @on-cancel="cancel">
                        <p></p>
                        <p></p>
                    </Modal>
                    <!--  -->
                    <div class="avatar" >
                        <a href="">
                            <span class="skin_portrait_round"></span>
                            <img :src="posting.info.avatarurl" alt="" height="100">
                        </a>
                    </div>
                    <!--  -->
                    <div class="box bgr" >
                        <!--  -->
                        <div class="arr bor" ></div>
                        <!--  -->
                        <div class="bd" >
                            <a class="nick" href="#">{{posting.info.nickname}}</a>
                            <pre class="content">{{posting.saycontent}}</pre>
                        </div>
                        <!--  -->
                        <div class="md" >
                            <div class="img-attachments">
                                <li v-for="(picurl,Picindex) in posting.picsurl" :key="Picindex" >
                                    <img :src="picurl" alt="">
                                    <br>
                                </li>
                            </div>
                        </div>
                        <!--  -->
                        <div class="ft" >
                            <div class="info">
                                <span class="c_tx">{{posting.addTime | formatDate}} {{posting.addTime | formatDateTwo}}</span>
                                <span class="c_tx">OPPO R9</span>
                            </div>
                            <div class="op">
                                <!--  -->
                                <span>
                                    <a href="#" v-if="showLike" @click="DoLike">({{posting.likes}})</a>
                                    <a href="#" v-if="!showLike" @click="DoLike"></a>
                                </span>
                                <!--  -->
                                <a href="#" @click="commentInput = true">({{posting.cnumber}})</a>
                                <!--  -->
                                <a href="#"></a>
                                <!--  -->
                                <div class="dropdown" @mouseover="WillShowMenu" @mouseout="NotShowMenu" >
                                    <a href="javascript:void(0);">
                                        
                                    </a>
                                    <div class="dropdown-menu" v-if="showMenu" >
                                        <ul>
                                            <li><a href="javascript:void(0);"></a></li>
                                            <li><a href="javascript:void(0);"></a></li>
                                            <li><a href="javascript:void(0);"></a></li>
                                            <li><a href="javascript:void(0);" @click="modal = true"></a></li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <!--  -->
                        <div class="box_extra bor" >
                            <div class="box_extra_arr"></div>
                            <div class="feed_like">
                                <Icon type="md-thumbs-up" class="tubiao" size="20"/>
                                <a href="javascript:void(0);" v-if="showZanMe"></a>
                                &nbsp;&nbsp;
                                <a href="javascript:void(0);" v-if="!showZanMe">{{posting.likes}}</a>
                                <a href="javascript:void(0);" v-if="showZanMe">{{posting.likes+1}}</a>
                                
                            </div><br>
                            <div class="mod_comment">
                                <!--  -->
                                <div class="mod_comments">
                                    <ul>
                                        <li v-for="(comment,commentindex) in posting.comments" :key="commentindex" class="comments_item">
                                            <div class="avatar">
                                                <a href="">
                                                    <span class="skin_portrait_round"></span>
                                                    <img :src="comment.avatarurl" alt="" height="100">
                                                </a>
                                            </div>
                                            <div class="comments_content">
                                                <div class="PC">
                                                    <div style="width:100%;height:10px;"></div>
                                                    <a href="javascript:void(0);">{{comment.nickname}} </a>
                                                    <span>{{comment.saything}}</span>
                                                </div>
                                                <div class="comments_op">
                                                    <span class="riqi">{{comment.date | formatDate}}</span>
                                                    <a href="javascript:void(0);"></a>
                                                </div>
                                            </div>
                                            <hr style=" height:1px;width:84%;margin:0px auto;margin-top:17px;border:none;border-top:1px dashed rgb(231, 231, 231);" />
                                        </li>
                                    </ul>
                                </div>
                                <br>
                                <!--  -->
                                <div class="pinglunSay">
                                    <textarea v-model="UserComment.saything" class="textarea"></textarea>
                                </div>
                                <!--   -->
                                <div>
                                    <div>
                                        <Button @click="submitCS(posting._id)" type="info" class="fabiao"></Button>
                                    </div>
                                </div>
                            </div>
                            
                        </div>
                    </div>
                    <br/>
                </li>
            </ul>
        </div>

        <!--  -->
        <div class="Pfooter">
            <br>
            <p>vue2.0 + express + mongodb</p>
            <p>Copyright  2005 - 2019 LXHguard.All Rights Reserved.</p>
            <p> </p>
        </div>

    </div>
</template>

<script>
import store from '../../../vuex/store'
import HeadTop from '../../components/HeadTop/HeadTop'
import {formatDate} from '../../../static/common/date.js' //date.js

export default {
    name:"Postings",
    //vuex
    store,
    //
    components: {
        HeadTop,
    },
    //
    data(){
        return{
            //
            postings:[],
            // 
            showLike:true,
            // 
            showMenu:false,
            // 
            showZanMe:false,
            //
            modal: false,
            //
            timer: null,
            //
            UserComment:{
                username:'',//
                saything:'',//
                id:'',//
            }

        }
    },
    //
    filters: {
       formatDate(time) {
            var date = new Date(time);
            return formatDate(date, 'yyyy.MM.dd');   //    'yyyy : MM : dd'   2018125
       },
       formatDateTwo(time) {
            var date = new Date(time);
            return formatDate(date, 'hh:mm:ss');   //  211212
        }
    },
    methods:{
        routerback(){//
            this.$router.back(-1);
        },
        DoLike(){//
            this.showLike = !this.showLike;
            this.showZanMe = !this.showZanMe;
        },
        WillShowMenu(){// 
            this.showMenu = true;
            clearInterval(this.timer)
        },
        NotShowMenu(){// 
            this.timer = setInterval( () => {
                this.showMenu = false;
    }, 100);
        },
        ok(id){// 
            //id:id
            var localtoken = localStorage.getItem("token");
            var localusername = localStorage.getItem("user_name");
            var localparams = {
                token:localtoken,
                user_name:localusername,
                id:id
            };
            this.$axios({
                url:"http://193.112.48.157:3333/users/deletesay",
                method:"post",
                data:localparams 
            })
            .then(res=>{
                if(res.data.status == 200){
                    this.$Message.info('!');
                    //
                    this.$axios({
                        url:"http://193.112.48.157:3333/users/postings",
                        method:"get",
                        params:localparams 
                    })
                    .then(res=>{
                        console.log('()vue[  ]');
                        console.log(res.data);
                        if(res.data.status == 200){
                            // 
                            this.postings = res.data.postings;
                            console.log(res.data.postings);
                        }else{
                            alert('');
                            this.$router.push('/login');
                        }
                    })
                    .catch(err=>{
                        console.log(err)
                    })//
                }else{
                    alert('');
                    this.$router.push('/login');
                }
            })
            .catch(err=>{
                console.log(err)
            })
        },
        cancel () {// 
            this.$Message.info('Clicked cancel');
        },
        submitCS(id){//
            var localusername = localStorage.getItem("user_name");
            this.UserComment.username = localusername;
            this.UserComment.id = id;
            this.$axios({
                url:"http://193.112.48.157:3333/users/submitcommont",
                method:"post",
                data:this.UserComment
            })
            .then(res=>{
                //
                this.UserComment.saything = '';
                this.$Message.info('');
                var localtoken = localStorage.getItem("token");
        var localusername = localStorage.getItem("user_name");
        var localparams = {
            token:localtoken,
            user_name:localusername
        };
          this.$axios({
                url:"http://193.112.48.157:3333/users/postings",
                method:"get",
                params:localparams 
              })
              .then(res=>{
                  console.log('()vue[  ]');
                  console.log(res.data);
                  if(res.data.status == 200){
                      // 
                      this.postings = res.data.postings;
                      console.log(res.data.postings);
                  }else{
                      alert('');
                      this.$router.push('/login');
                  }
              })
              .catch(err=>{
                  console.log(err)
              })
            })
            .catch(err=>{
                console.log(err)
            })
        }

    },//
    /**
     * 
     */
    //beforeCreate    data(){} 
    beforeCreate(){
        //token
        var localtoken = localStorage.getItem("token");
        var localusername = localStorage.getItem("user_name");
        var localparams = {
            token:localtoken,
            user_name:localusername
        };
          this.$axios({
                url:"http://193.112.48.157:3333/users/postings",
                method:"get",
                params:localparams 
              })
              .then(res=>{
                  console.log('()vue[  ]');
                  console.log(res.data);
                  if(res.data.status == 200){
                      // 
                      this.postings = res.data.postings;
                      console.log(res.data.postings);
                  }else{
                      alert('');
                      this.$router.push('/login');
                  }
              })
              .catch(err=>{
                  console.log(err)
              })
    },//beforreCreate-end


}
</script>

<style scoped>
#postings{
    background-color: #e9e9e9;
}
/*  */
.Pcontainer{
    padding:20px;
    width:65%;
    margin:0 auto;
    background-color: #fff;
    border-radius: 10px;
}
/*  */
ul,li{
    list-style: none;
}
/*  */
.feed{
    margin-bottom: -1px;
    position: relative;
    padding: 0 0 10px 70px;
    line-height: 1.5;
    min-height: 55px;
    word-wrap: break-word;
    display: list-item;

}
/*  */
.avatar{
    position: absolute;
    left: 0;
    top: 0;
    box-shadow: 0 1px 3px rgba(0,0,0,.3);
    border-radius: 4px;
}
.avatar a{
    width: 50px;
    height: 50px;
    display: block;
    overflow: hidden;
    border-radius: 4px;
    text-decoration: none;
    color: #295c9d;
    cursor: pointer;
}
.avatar .skin_portrait_round {
    position: absolute;
    left: 0;
    top: 0;
    width: 50px;
    height: 50px;
    background-position: 0 -40px;
}
/*  */
.box {
    position: relative;
    border-radius: 5px;
}
.bgr{
    border-style: solid;
    border-width: 1px;
    border-color: #e7e7e7;
}
/*  */
.arr{
    border-bottom-width:  7px !important;
    border-bottom-style: dashed !important;
    border-bottom-color: transparent !important;
    border-left-width: 7px !important;
    border-left-style: dashed !important;
    border-left-color: transparent !important;
    border-top-width: 7px !important;
    border-top-style: dashed !important;
    border-top-color: transparent !important;
    border-right-width: 7px;
    font-size: 0;
    width: 0;
    position: absolute;
    left: -14px;
    top: 15px;
}
/*  */
.bd{
    font-size: 14px;
    padding: 9px 15px 0;
    line-height: 25px;
    margin-bottom: 8px;
    text-align:left;
}
.nick{
    margin-right: 8px;
    color: #295c9d;
    text-decoration: none;
    font-size: 14px;
    line-height: 25px;
    cursor: pointer;
}
.content{
    display: inline;
    white-space: pre-wrap;
    font-family: punctuation,'Microsoft Yahei';
    line-height: 25px;
    font-size: 14px;
}
.bor{
    border-style: solid;
    border-color: #e7e7e7;
}
/*  */
.md{
    padding: 0 15px;
    margin: 0;
    display: block;
    line-height: 1.5;
    color: #000;
}
.img-attachments{
    font-size: 0;
    position: relative;
    margin-bottom: -8px;
    zoom: 1;
}
.img-attachments img{
    vertical-align: top;
    cursor: pointer;
    border: 0;
    width: 50%;
    margin-bottom:20px;
}
.ft{
    padding: 3px 15px 6px;
    height: 20px;
    margin-bottom:8px;
}
.info{
    float: left;
}
.op{
    float: right;
    position: relative;
}
.op a{
    margin-left: 10px;
    color: #295c9d;
}
.c_tx{
    color: #777;
    margin-right: 10px;
    display: inline-block;
    vertical-align: top;
}
.dropdown {
    display: inline-block;
    position: relative;
    z-index: 40;
}
.dropdown a{
    margin-left: 10px;
    position: relative;
    z-index: 30;
    outline: 0 none;
}
.dropdown-menu{
    position: absolute;
    top: -9px;
    right: -15px;
    border-width: 1px;
    z-index: 29;
    padding-top: 30px;
    border-style: solid;
    border-color: #e7e7e7;
    background: #fff;
    color: #000;
}
.dropdown-menu ul{
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}
.dropdown-menu ul li{
    margin: 0 ;
    padding: 0;
    display: list-item;
}
.dropdown-menu ul li:hover{
    background-color: #e7e7e7;
}
.dropdown-menu ul li a{
    display: block;
    width: 50px;
    height: 32px;
    line-height: 32px;
    margin: 0!important;
}
.box_extra{
    padding: 15px;
    border-width: 1px 0 0;
    position: relative;
    border-style: solid;
    border-color: #e7e7e7;
}
.box_extra_arr{
    position: absolute;
    left: 30px;
    top: -8px;
    width:0;
    height:0;
	border-right:10px solid transparent;
	border-left:10px solid transparent;
	border-bottom:8px solid #e7e7e7;
}
.feed_like{
    padding: 3px 15px 6px;
    height: 20px;
    text-align: left;
}
.tubiao{
    color: royalblue;
}
.Pfooter{
    background-color: #e7e7e7;
    width:100%;
    text-align: center;
}
.comments_item{
    border: none;
    padding-top: 0;
    margin-top: 0;
    position:relative;
}
.ui_avatar{
    position: absolute;
    left: 0;
    top: 0;
}
.comments_content{
    position: relative;
    top: -4px;
    min-height: 35px;
    line-height: 1.6;  
    margin-bottom: -4px;
    text-align: left;
}
.PC{
    margin-left:60px;
    position: relative;
}
.comments_content a{
    margin-right: 8px;
    text-decoration: none;
    color: #295c9d;
    text-align: left;
    font-size:12px;
}
.comments_op{
    margin-left:60px;
    padding-top: 5px;
    line-height: 18px;
    position: relative;
    margin-bottom: 12px;
}
.riqi{
    margin-right: 10px;
    color: #777;
    line-height: 18px;
}
.textarea{
    width:60%;
    height: 80px;
    line-height: 20px;
    padding: 3px 8px 5px;
    background: none;
    font-size: 14px;
    box-shadow: inset 0 2px 4px 0 #E9E9E9;
    border: 1px solid #E8E8e8;
    outline:none;/**/
    resize:none;/**/
}
.pinglunSay{
    position: relative;
}
.fabiao{
    position: relative;
    top:0;
    right:-26%;
}
</style>



